.App {
    text-align: center;
    top: 0;
    height: auto;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    align-items: flex-start;
    justify-content: flex-start;
    font-size: calc(11px + 2vmin);
  }
  .App-link {
    color: #61dafb;
    width: 100vw;
    background-color: #282c34;
  }
  .menu-header {
    width: 100vw;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .topics {
    width: 20%;
    margin-left: 5%;
    text-align: start;
  }
  .topic-name{
    padding: 5%;
  }
  .subtopics{
    position: absolute;
    background-color: #282c34;
    padding: 20px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    font-size: calc(9px + 2vmin);
  }
  .subtopic-name{
    display: flex;
    align-items: center;
    width: 100%;
    cursor: pointer;
    margin-bottom: 5px;
  }
  .subtopic-name-heading{
    width: 100%;
    text-align: start;
  }
  .subtopics2{
    display: flex;
    flex-direction: column;
    position: absolute;
    margin-left: 15%;
    align-content: flex-start;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  @media (max-width: 500px) {
    .App-header {
      font-size: 1rem;
    }
    .topic-name{
      padding: 1.5%;
    }
    .topics {
      width: 15%;
      margin-left: 5%;
      text-align: center;
    }
    .menu-header {
      width: 95vw;
      justify-content: space-evenly;
    }
  }